import { faAt, faEnvelope, faMapMarkerAlt, faMobile } from "@fortawesome/free-solid-svg-icons"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import React from "react"
import { Col, Container, Row } from "react-bootstrap"

export const ContactView = ()=>{
    return <React.Fragment>
        <Container fluid style={{paddingTop:"9rem",paddingBottom:"9rem"}}>
            <Row>
                <Col md={6} >
                    <div className="Contact-Title" style={{paddingLeft:"9rem"}}>
                        Contacteaza-ne
                    </div>
                    <div className="Contact-SubText" style={{paddingLeft:"8rem"}}>
                        Ai vreo intrebare sau curiozitate?<br/>
                        Ne gasesti aici:
                    </div>
                    <div className="Contact-AddressPoint">
                        <div>
                            <FontAwesomeIcon icon={faMapMarkerAlt} size="3x" />
                        </div>
                        <div className="Contact-Data">
                            <div className="Contact-Header">Adresa</div>
                            <div className="Contact-Text" style={{paddingTop:"1rem"}}>Str. Electrolizei nr.2, Timisoara, jud. Timis,<br/>
                            300645</div>
                            
                        </div>
                    </div>

                    <div className="Contact-AddressPoint">
                        <div>
                            <FontAwesomeIcon icon={faMobile} size="3x" />
                        </div>
                        <div className="Contact-Data">
                            <div className="Contact-Header">Numar de Telefon</div>
                            <div className="Contact-Text" style={{paddingTop:"1rem"}}>
                                +407123456789<br/>
                                +407123456789
                            </div>
                            
                        </div>
                    </div>

                    <div className="Contact-AddressPoint">
                        <div>
                            <FontAwesomeIcon icon={faEnvelope} size="3x" />
                        </div>
                        <div className="Contact-Data">
                            <div className="Contact-Header">Email</div>
                            <div className="Contact-Text" style={{paddingTop:"1rem"}}>
                                Alexandru.Toplean@topsoftsystems.com<br/>
                                Daniel.Borlea@topsoftsystems.com<br/>
                                Contact@topsoftsystems.com<br/>
                            </div>
                            
                        </div>
                    </div>

                </Col>
                <Col md={6}>
                    <div className="Contact-Title">
                        Lasa-ne un feedback!
                    </div>

                    <div>
                        <div className="form__group">
                            <input type="text" className="form__input" id="name" placeholder="Numele" required />
                            <label htmlFor="name" className="form__label">Numele</label>
                        </div>

                    </div>
                    <div>
                    <div className="form__group">
                            <input type="text" className="form__input" id="name" placeholder="Adresa de E-mail" required />
                            <label htmlFor="name" className="form__label">Adresa de E-mail</label>
                        </div>
                    </div>
                    <div>
                    <div className="form__group">
                            <textarea  className="form__input textarea" id="name" placeholder="Scrie aici" required >

                            </textarea>
                           
                            <label htmlFor="name" className="form__label">Scrie aici</label>
                        </div>
                    </div>
                    <div style={{marginLeft:"3rem"}}>
                        <button className="btn btn-purple">Trimite</button> 
                    </div>
                </Col>
            </Row>
        </Container>

    </React.Fragment>
}