import axios from "axios";
import { UserRegisterCredentials } from "../../Models/User/UserRegisterCredentials";
import { handleError } from './apiUtils';

const axiosClient = axios.create({
    baseURL:"https://localhost:44376/api"
});

export const loginGet = ()=>axiosClient.get("Account/Login");
export const loginPost = (headers:any) => axiosClient.post("Account/Login",null,{headers:headers});

export const verifyToken = (headers:any)=>axiosClient.post("Account/VerifyToken",null,{headers});

export const registerPost = (user:UserRegisterCredentials)=>axiosClient.post("Account/Register",user);
