import React, { useEffect, useRef } from "react";
import { useState } from "react";
import {
  Button,
  Collapse,
  Container,
  Form,
  FormControl,
  Modal,
  Nav,
  Navbar,
  NavDropdown,
} from "react-bootstrap";

import API from "../../Content/API/API.json";
import {
  loginPost,
  registerPost,
} from "../../Services/Axios/axiosConnectService";
import { User } from "../../Models/User";
import { JsonService } from "../../Services/JSON/JsonService";
import { UserRegisterCredentials } from "../../Models/User/UserRegisterCredentials";
import { LocalStorageService } from "../../Services/Storage/LocalStorageService";
import { Link, Redirect } from "react-router-dom";

import CircularProgress from '@material-ui/core/CircularProgress';

import TopSoftIconLight from "../../Content/assets/png/Logo White.png";
import TopSoftIconDark from "../../Content/assets/png/Logo Black.png";

import RomanianFlag from "../../Content/assets/png/flags/romania.png";
import LandOfTheFreeFlag from "../../Content/assets/png/flags/united-states-of-america.png";
import FranceFlag from "../../Content/assets/png/flags/france.png";


type HeaderBarProps = {
  language: String;
  setLanguage?: Function;
  user?: User;
  setUser: React.Dispatch<React.SetStateAction<User | undefined>>;
};

export const HeaderBar = (props: HeaderBarProps) => {
  const jsonService = JsonService.getInstance();
  const localStorageService = LocalStorageService.getInstance();

  const wrapper = useRef("idk");

  const [language, setLanguage] = useState(localStorageService.getCurrentOrDefaultLanguage());

  // const [openRegister, setOpenRegister] = useState(false);
  const [redirect, enableRedirect] = useState<boolean>(false);
  const [openLogin, setOpenLogin] = useState<boolean>(false);
  const [openRegister, setOpenRegister] = useState<boolean>(false);



  const [loginUsername, setLoginUsername] = useState<string>("");
  const [loginPassword, setLoginPassword] = useState<string>("");
  const [loginRememberMe, setLoginRememberMe] = useState<boolean>(false);

  const [registerUsername, setRegisterUsername] = useState<string>("");
  const [registerPassword, setRegisterPassword] = useState<string>("");
  const [registerConfirmPassword, setRegisterConfirmPassword] = useState<string>("");
  const [registerEmail, setRegisterEmail] = useState<string>("");

  const handleClose = () => {
    setOpenLogin(false);
    setOpenRegister(false);
  };
  const handleLoginSubmit = () => {
    var encodedString = Buffer.from(
      `${loginUsername}:${loginPassword}`
    ).toString("base64");
    var headers = {
      Authorization: "Basic " + encodedString,
      RememberMe: loginRememberMe,
    };

    const loginProgress = document.getElementById("loginProgress");
    loginProgress? loginProgress.style.display = "block":void 0;

    loginPost(headers).then((response: any) => {
       
      if (response && response.status === 200) {
        var client = response.data;
        props.setUser(client as User);
        localStorageService.logInUser(client.token);
        loginProgress? loginProgress.style.display = "none":void 0;
        handleClose();
        return;
      }
      //TODO: Error message for error code 400 and 500
    }).catch((error:any)=>{
       ;
    });
  };

  useEffect(()=>{
    redirect? enableRedirect(false):void 0;
  },[redirect])

  const handleLogout = ()=>{
    localStorageService.logOutUser();
    props.setUser(undefined);
    enableRedirect(true);
  }

  const handleLoginRegister = () => {
    debugger;
    const user: UserRegisterCredentials = {
      username: registerUsername,
      password: registerPassword,
      confirmPassword: registerConfirmPassword,
      emailAddress: registerEmail,
    };
     
    registerPost(user).then((response: any) => {
      debugger;
      const registerProgress = document.getElementById("registerProgress");
      registerProgress? registerProgress.style.display = "block":void 0;
      if (response && response.status < 300) {
        props.setUser(response.data);
        localStorageService.logInUser(response.data.token);
        registerProgress? registerProgress.style.display = "none":void 0;
        handleClose();
        return;
      }
    }).catch((ex:any)=>{debugger;});
  };

  return (
    <React.Fragment>
      {redirect? <Redirect to="/"/>:void 0}
      <Navbar className="blackBG" expand="lg" sticky="top">
        <Navbar.Brand style={{paddingLeft:"4rem"}}>
          <Link to="/">
            <img src={ (window.matchMedia('(prefers-color-scheme: dark)').matches)? TopSoftIconLight : TopSoftIconDark} height="70rem" alt="NavIcon"/>
          </Link>    
           
         
        </Navbar.Brand>
        
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">

        <Nav className="ml-auto my-2 my-lg-0" >
            <Nav.Link><Link to="/AboutUs" ><span className="menuOption">{jsonService.getTextInLang("AboutUs")}</span></Link></Nav.Link>
            <Nav.Link><Link to="/Services" ><span className="menuOption">{jsonService.getTextInLang("OurServices")}</span></Link></Nav.Link>
            <Nav.Link><Link to="/Contact" ><span className="menuOption">{jsonService.getTextInLang("Contact")}</span></Link></Nav.Link>
        </Nav>

        <Nav className="ml-auto my-2 my-lg-0" >
          {props.user?<React.Fragment>
            <NavDropdown title={
              <span className="menuOption">{props.user.username}</span>} id="basic-nav-dropdown">
          <NavDropdown.Item><Link to="/Account">{jsonService.getTextInLang("Settings")}</Link></NavDropdown.Item>
          <NavDropdown.Item ><Link to="/Account/ProductInfo">{jsonService.getTextInLang("ProductInfo")}</Link></NavDropdown.Item>
          <NavDropdown.Item ><Link to="/Reports">{jsonService.getTextInLang("Reports")}</Link></NavDropdown.Item>
          <NavDropdown.Divider />
          <NavDropdown.Item  onClick={handleLogout}>{jsonService.getTextInLang("Logout")}</NavDropdown.Item>
        </NavDropdown>
          </React.Fragment>
          :
          <React.Fragment>
            <Nav.Link><span className="menuOption" onClick={()=>{setOpenLogin(true)}}>{jsonService.getTextInLang("Login")}</span></Nav.Link>
            <Nav.Link><span className="menuOption" onClick={()=>{setOpenRegister(true)}}>{jsonService.getTextInLang("Register")}</span></Nav.Link>
          </React.Fragment>
          }
            <NavDropdown alignRight  title={
              <span className="menuOption">{jsonService.getTextInLang("Language")}</span>} id="navbarScrollingDropdown">
              <NavDropdown.Item href="#action3" className={language === "ro"?"activeFlag":void 0} onClick={()=>{localStorageService.setCurrentLanguage("ro");setLanguage("ro")}}> <img src={RomanianFlag} width="100%"/> </NavDropdown.Item>
              <NavDropdown.Item href="#action4" className={language === "en"?"activeFlag":void 0} onClick={()=>{localStorageService.setCurrentLanguage("en");setLanguage("en")}}><img src={LandOfTheFreeFlag} width="100%"/></NavDropdown.Item>
              <NavDropdown.Item href="#action4" className={language === "fr"?"activeFlag":void 0} onClick={()=>{localStorageService.setCurrentLanguage("fr");setLanguage("fr")}}><img src={FranceFlag} width="100%"/></NavDropdown.Item>
              
          </NavDropdown>
        </Nav>
          
        </Navbar.Collapse>
      </Navbar>

     
      <Modal show={openLogin} onHide={handleClose} ref={wrapper}>
        <Modal.Header closeButton>
          <Modal.Title> {jsonService.getTextInLang("Connect")}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form
            method="post" id="loginForm"action={API.host + "Account/Login"}>
            <Form.Group controlId="loginUsername">
              <Form.Label>{jsonService.getTextInLang("Username")}</Form.Label>
              <Form.Control type="text" onChange={(e) => setLoginUsername(e.target.value)} placeholder="Username..."/>
            </Form.Group>
            <Form.Group controlId="loginPassword">
              <Form.Label>{jsonService.getTextInLang("Password")}</Form.Label>
              <Form.Control
                type="password"
                onChange={(e) => setLoginPassword(e.target.value)}
                placeholder="Password..."
              />
            </Form.Group>
            <Form.Check
              type="checkbox"
              id={`rememberMe`}
              onChange={(e) => setLoginRememberMe(e.target.checked)}
              label={jsonService.getTextInLang("KeepMeLogged")}
            />
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            {jsonService.getTextInLang("Close")}
          </Button>
          <Button variant="primary" onClick={() => handleLoginSubmit()}>
            {jsonService.getTextInLang("Login")}
          </Button>
          <CircularProgress style={{display:"none"}} id="loginProgress" />
        </Modal.Footer>
      </Modal>

      <Modal show={openRegister} onHide={handleClose} ref={wrapper}>
        <Modal.Header closeButton>
          <Modal.Title> {jsonService.getTextInLang("Register")}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form
            method="post"
            id="loginForm"
            validated = {true}
            action={API.host + "Account/Login"}
          >
            <Form.Group controlId="registerUsername">
              <Form.Label>{jsonService.getTextInLang("Username")}</Form.Label>
              <Form.Control
                type="text"
                onChange={(e) => setRegisterUsername(e.target.value)}
                placeholder="Username..."
              />
              <Form.Control.Feedback hidden={false}>Hello</Form.Control.Feedback>

            </Form.Group>
            <Form.Group controlId="registerPassword">
              <Form.Label>{jsonService.getTextInLang("Password")}</Form.Label>
              <Form.Control
                type="password"
                onChange={(e) => setRegisterPassword(e.target.value)}
                placeholder="Password..."
              />
              <Form.Control.Feedback hidden={true}>Hello</Form.Control.Feedback>

            </Form.Group>
            <Form.Group controlId="registerConfirmPassword">
              <Form.Label>
                {jsonService.getTextInLang("ConfirmPassword")}
              </Form.Label>
              <Form.Control
                type="password"
                onChange={(e) => setRegisterConfirmPassword(e.target.value)}
                placeholder="Password..."
              />
            </Form.Group>
            <Form.Group controlId="registerEmailAddress">
              <Form.Label>{jsonService.getTextInLang("Email")}</Form.Label>
              <Form.Control
                type="text"
                onChange={(e) => setRegisterEmail(e.target.value)}
                placeholder="Email@test.com"
              />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            {jsonService.getTextInLang("Close")}
          </Button>
          <Button variant="primary" onClick={() => handleLoginRegister()}>
            {jsonService.getTextInLang("Register")}
          </Button>
          <CircularProgress style={{display:"none"}} id="registerProgress" />

        </Modal.Footer>
      </Modal>
    </React.Fragment>
  );
};
