export class LocalStorageService{
    private static Instance:LocalStorageService;
    private LocalStorage(){}

    public static getInstance(){
        if(!LocalStorageService.Instance){
            LocalStorageService.Instance = new LocalStorageService();
        }

        return LocalStorageService.Instance;
    }


    //#region Languages

    public getCurrentOrDefaultLanguage(){
        return localStorage.getItem("topsoft_currentLanguage") ?? "ro";
    }

    public setCurrentLanguage(value:string){
        localStorage.setItem("topsoft_currentLanguage",value);
    }


    public logOutUser(){
        localStorage.removeItem("topsoft_token");
    }

    public logInUser(token:string){
        localStorage.setItem("topsoft_token", token);
    }
    
    //#endregion

    
}