import { LocalStorageService } from './../Storage/LocalStorageService';
import MainLanguageJson from "./../../Content/Language/LanguageMainJson.json"




export class JsonService{
    private static Instance:JsonService;
    private localStorageService:LocalStorageService = LocalStorageService.getInstance();

    private constructor(){ }

    public static getInstance(){
        if(!JsonService.Instance){
            JsonService.Instance = new JsonService();
        }
        return JsonService.Instance;
    }

    public getTextInLang = (key : String) : String => {
       // @ts-ignore
       return MainLanguageJson[this.localStorageService.getCurrentOrDefaultLanguage()][key] as String;

    }

}

