import { Paper } from "@material-ui/core"

type SubscriptionCardProps = {
    subscriptionType:string,
    subscriptionInfo:string
    subscriptionOffers:Array<string>,
    hasTrial?:boolean,
}
export const SubscriptionCard = (props:SubscriptionCardProps) =>{
    return <Paper elevation={3} className="paperCard">
    <div style={{textAlign:"center"}} className="subscription">
        <div className="subscriptionType">
            {props.subscriptionType}
        </div>
        <div className="subscriptionInfo">
            {props.subscriptionInfo}
        </div>

        <div className="subscriptionInfo">
            {props.subscriptionOffers.map((offer:any) => <div>- {offer}</div>)}
        </div>
        <div className="actionBtnContainer">
            {props.hasTrial?<div className="padding actionBtn">
                <button className="btn cardBtn trySubscriptionBtn" >Incearca</button>
            </div>:void 0}
            
            <div className="actionBtn">
                <button className="btn cardBtn buySubscriptionBtn" >Cumpara</button>
            </div>
        </div>
       

    </div>
</Paper>
}